import counter from './counter.vue'
export default {
	components: {
		counter
	},
	methods: {
		hideSpecifications() {
			this.$emit("hideSpec");
		},
		handleSelectSpecification(item) {
			// 子级注册事件 父级执行事件
			this.$emit("setSelectSpec",item);
		},
		sub(){
			this.$emit("sub");
		},
		add(){
			this.$emit("add");
		}
	},
	props: {
		spaceInfo: Object,
		goodsInfo: Object,
		goodsData: Object
	}
}
